<template>
    <div class="start">
        <section>
            <h1 class="title" style="color:#515a6e">快速上手</h1>
            <h2>使用之前</h2>
            <div class="des">
                在使用<span class="code">Xue-ui</span>之前，我们假设您已经了解<span class="code">Vue</span>、<span class="code">Vue</span>组件、<span class="code">Vue</span>单文件组件的基础知识。
            </div>
            <h2>使用构建工具</h2>
            <p class="des">
                一般在<span class="code">webpack</span>入口页面<span class="code">main.js</span>中如下配置：</p>
            <pre class="code">
<span class="key">import</span> Vue <span class="key">from</span> <span class="str">'vue'</span>;
<span class="key">import</span> App <span class="key">from</span> <span class="str">'components/app.vue'</span>;
<span class="key">import</span> Xue <span class="key">from</span> <span class="str">'xue-ui'</span>;
<span class="key">import</span> </span> <span class="str">'xue-ui/lib/xue-ui.css'</span>;
Vue.use(Xue);
<span class="key">new</span> Vue({
    el: <span class="str">'#app'</span>,
    render: h => h(App)
});
</pre>
            <p class="des">以上代码便完成了<span class="code">Xue-ui</span>的引入。需要注意的是，样式文件需要单独引入。</p>
            <h2>特别提醒</h2>
            <p class="des">
                使用<span class="code">Xue-ui</span>时，您需要使用<span class="code">border-box</span>盒模型，否则会影响样式。<span class="code">CSS</span>代码示例：</p>
            <pre class="code">
*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}</pre>
            <p class="des"> 使用<span class="code">:prop</span>传递数据格式为数字、布尔值时，必须带<span class="code">:</span>(兼容<span class="code">String</span>除外，具体看组件文档)，比如：</p>
            <pre class="code">
<span class="str">正确的用法：</span>
<span class="tag">&lt;x-pager</span> <span class="key">:total</span><span class="tag">=</span><span class="str">"100"</span> <span class="key">:current.sync</span><span class="tag">=</span><span class="str">"1"</span><span class="tag">&gt;&lt;/x-pager&gt;</span>
<span class="key" style="color:#f5222d;">错误的用法：</span>
<span class="tag">&lt;x-pager</span> <span class="key">total</span><span class="tag">=</span><span class="str">"100"</span> <span class="key">current</span><span class="tag">=</span><span class="str">"1"</span><span class="tag">&gt;&lt;/x-pager&gt;</span>            
</pre>
